p {
  margin: 0;
  font-size: 1rem;
}

.App {
  text-align: left;
  padding: 8px 24px 0 32px;
  white-space: pre-line;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 40px;
  margin-bottom: 10px;
}

.heart {
  height: 60px;
  width: 60px;
  margin-left: auto;
  text-align: center;
}

.small-font {
  font-size: 0.75rem;
}
